import { createRouter, createWebHistory } from 'vue-router'
import HomeView from '../views/HomeView.vue'

// Умения
import Backend from '../views/hardskills/BackendView.vue'
import Frontend from '../views/hardskills/FrontendView.vue'
import Graph from '../views/hardskills/GraphView.vue'
import CMS from '../views/hardskills/CMSView.vue'
import Python from '../views/hardskills/PythonView.vue'
import Layout from '../views/hardskills/LayoutView.vue'
// Проекты
import SoftGlass from '../views/projects/SoftGlassView.vue'
import DMB from '../views/projects/DMBView.vue'
import BlockCalc from '../views/projects/BlockCalcView.vue'

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView,
  },
  // Projects
  {
    path: '/soft-glass',
    name: 'soft-glass',
    component: SoftGlass,
    meta: {
      category: 'projects',
    }
  },
  {
    path: '/dmb-crm',
    name: 'dmb',
    component: DMB,
    meta: {
      category: 'projects',
    }
  },
  {
    path: '/stroy-sodruzhestvo',
    name: 'block-calc',
    component: BlockCalc,
    meta: {
      category: 'projects',
    }
  },
  // Hardskills
  {
    path: '/layout',
    name: 'layout',
    component: Layout,
    meta: {
      category: 'hardskills',
    }
  },
  {
    path: '/frontend',
    name: 'frontend',
    component: Frontend,
    meta: {
      category: 'hardskills',
    }
  },
  {
    path: '/backend',
    name: 'backend',
    component: Backend,
    meta: {
      category: 'hardskills',
    }
  },
  {
    path: '/python',
    name: 'python',
    component: Python,
    meta: {
      category: 'hardskills',
    }
  },
  {
    path: '/graphic',
    name: 'graphic',
    component: Graph,
    meta: {
      category: 'hardskills',
    }
  },
  {
    path: '/cms',
    name: 'cms',
    component: CMS,
    meta: {
      category: 'hardskills',
    }
  },
]

const router = createRouter({
  history: createWebHistory(),
  routes
})

export default router
