<template>
  <main class="home" @scroll="scrollFunc" @resize="resizeFunc">
    <div class="home__controls" :class="{ 'active': activeSection > 0 }">
      <a v-for="cat in categories" :class="{ 'active': cat.id === activeSection }" :href="cat.element" class="item"></a>
    </div>
    <h2 v-for="cat in categories" class="side__title" :class="{ 'active': activeSection === cat.id }">
      {{ cat.title }}
    </h2>

    <Intro id="intro" />
    <HardSkills id="hardskills" @openPage="(value) => openPage(value)" :class="{'unmounted': unmounted}" />

    <Projects id="projects" @openPage="(value) => openPage(value)" />
    <Progress id="progress" />

  </main>
</template>

<script>

import Intro from '@/home/Intro.vue';
import HardSkills from '@/home/HardSkills.vue';
import Progress from '@/home/Progress.vue';
import Projects from '@/home/Projects.vue';

import { ref, onMounted, onBeforeUnmount } from 'vue';
import { useRouter, useRoute } from 'vue-router'
export default {
  components: {
    Intro,
    HardSkills,
    Progress,
    Projects,
  },

  setup() {
    const unmounted = ref(false)
    const activeSection = ref(0)
    const categories = ref([
      {
        id: 3,
        position: 0,
        title: 'HARDSKILLS',
        element: '#hardskills',
      },
      {
        id: 2,
        position: 0,
        title: 'PROJECTS',
        element: '#projects',
      },
      {
        id: 1,
        position: 0,
        title: 'PROGRESS',
        element: '#progress',
      },
    ])
    const route = useRoute()
    const router = useRouter()

    // Открытие страницы
    function openPage(link) {
      unmounted.value = true
      router.push({ name: link })
    }

    // Событие скролла
    function scrollFunc(e) {
      const scroll = e.target.scrollTop
      for (let i = categories.value.length-1; i >= 0; i--) {
        const cat = categories.value[i];
        if (scroll >= cat.position) {
          activeSection.value = cat.id
          return
        }
      }
      activeSection.value = 0
    }

    // Событие изменения окна браузера
    function resizeFunc() {
      let firstSection = true
      categories.value.map(cat => {
        let y = document.querySelector(cat.element).offsetTop
        if (firstSection) {
          firstSection = false
          cat.position = y
        } else {
          let winHeight = window.innerHeight / 2
          if (winHeight > 300) {
            winHeight = 300
          }
          cat.position = y - winHeight
        }
      });
    }

    onMounted(() => {
      window.addEventListener('resize', resizeFunc)
      if (route.hash) {
        console.log(route.hash);
        document.querySelector(route.hash).scrollIntoView();
      }
      resizeFunc()
    })

    onBeforeUnmount(()=> {
      window.removeEventListener('resize', resizeFunc)
    })



    return {
      unmounted,
      categories,
      activeSection,

      openPage,
      scrollFunc,
      resizeFunc,
    }
  },
}
</script>

<style lang="scss">
#hardskills,
#projects,
#progress {
  padding-top: 200px;
  padding-bottom: 50px;
}
#progress {
  margin-top: 150px;
}

.home__controls {
  position: fixed;
  top: 0;
  right: 0;
  padding: 23px;
  display: flex;
  flex-direction: column;
  gap: 12px;
  z-index: 99;
  transform: translateX(200%);
  transition: transform .4s;

  .item {
    width: 18px;
    height: 36px;
    border-radius: 9px;
    background-color: var(--color-dark3);
    transition: background-color .2s;

    &.active {
      background-color: var(--color-accent);
    }
  }
}

.home__controls.active {
  transform: none;
}

.home .side__title {
  transition: transform .4s;
  transform: translateX(200%) rotate(180deg);

  &.active {
    transform: rotate(180deg);
  }
}


@media (max-width: 500px) {
  .home__controls {
    display: none;
  }
}

@media (max-height: 620px) {
  .home .side__title {
    display: none;
  }
}

</style>
