<template>
  <div class="progress__wrapper">
    <div class="progress__img">
      <img :src="imgSrc" />
    </div>
    <div class="progress__info">
      <h4 class="year">{{year}}</h4>
      <hr />
      <p>
        <slot />
      </p>
    </div>
  </div>
</template>

<script>
import { computed } from 'vue'
export default {
  props: {
    year: [Number, String],
    img: String,
  },
  setup(props) {
    const imgSrc = computed(() => require(`@/assets/img/progress/${props.img}`))
    return {imgSrc}
  }
}
</script>

<style lang="scss">
.progress__wrapper {
  --progress-gap: 70px;
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
  width: 100%;
  justify-content: space-between;
}
.progress__wrapper.reverse {
  flex-direction: row;
  .year {
    padding-left: var(--progress-gap);
  }
  p {
    padding-left: var(--progress-gap);
    padding-right: 0;
  }
}
.progress__info {
  width: 50%;
  .year {
    font-weight: 600;
    font-size: 60px;
  }
  p {
    width: 100%;
    padding-right: var(--progress-gap);
    text-align: left;
  }
  hr {
    height: 3px;
    background-color: #fff;
    margin-top: 15px;
    margin-bottom: 15px;
  }
}
// Диплом
.progress__img {
  width: calc(50% - var(--progress-gap));
  position: relative;
  img {
    width: 100%;
    object-fit: contain;
  }
  &:after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    height: 50%;
    background: linear-gradient(0deg, rgba(0, 0, 0, .7) 0%, rgba(0, 0, 0, 0) 100%);
  }
}

@media (max-width: 1000px) {
  .progress__wrapper {
    align-items: start;
  }
  .progress__wrapper {
    --progress-gap: 35px;
  }
}
@media (max-width: 750px) {
  .progress__wrapper,
  .progress__wrapper.reverse {
    flex-direction: column-reverse;
    gap: var(--progress-gap);

    .progress__info {
      width: 100%;
      p {
        width: 100%;
        padding-right: var(--progress-gap);
        padding-left: 0;
      }
      .year {
        padding-left: 0;
      }
    }
  }
  .progress__img {
    width: calc(100% - var(--progress-gap));
  }
  
}
</style>
