<template>
  <a :href="link" :class="{ 'preview-crop': previewCrop }" target="_blank" class="layout__project" :style="{ 'backgroundColor': backgroundColor }">
    <img class="preview" :src="previewSrc">
    <img class="logo" :style="{ width: logoWidth + 'px' }" :src="logoSrc">
  </a>
</template>

<script>
import { computed } from 'vue'
export default {
  props: {
    preview: String,
    logo: String,
    link: String,
    logoWidth: {
      type: Number,
      default: 200,
    },
    previewCrop: {
      type: Boolean,
      default: false,
    },
  },
  setup(props) {
    const previewSrc = computed(() => require(`@/assets/img/layouts/${props.preview}`))
    const logoSrc = computed(() => require(`@/assets/img/layouts/${props.logo}`))

    return {
      previewSrc,
      logoSrc,
    }
  }
};
</script>

<style lang="scss">
.layout__project {
  position: relative;
  max-width: 600px;
  width: calc(50% - 15px);
  height: 400px;
  display: flex;
  align-items: center;
  justify-content: end;
  overflow: hidden;

  &::after {
    content: '';
    position: absolute;
    width: 100%;
    height: 25%;
    bottom: -1px;
    background: linear-gradient(0deg, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
  }

  .preview {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: left;
  }

  .logo {
    position: absolute;
    left: 30px;
    width: 200px;
  }
}

@media (max-width:1100px) {
  .layout__project {
    height: 300px;

    .logo {
      left: 20px;
      max-width: 150px;
    }
  }
}

@media (max-width:900px) {
  .layout__project {
    width: 100%;
    height: 350px;

    .logo {
      left: 30px;
      max-width: 200px;
    }
  }
}
@media (max-width:650px) {
  .layout__project {
    width: 100%;
    height: 300px;

    .logo {
      left: 20px;
      max-width: 200px;
    }
  }
}
@media (max-width:550px) {
  .layout__project {
    height: 230px;
  }
  .layout__project.preview-crop {
    background-color: #E1E6EA;
    .preview {
      display: none;
      margin-top: 20px;
    }
  }
}
</style>