<template>
  <main>
    <h2 class="side__title">GRAPHIC</h2>
    <section class="section graph section__paddings">
      <div class="container graph__container">
        <h3 class="title-2">Навыки</h3>
        <ul class="graph__list">
          <li v-for="skill in skills">{{ skill }}</li>
        </ul>

        <h3 class="title-2">Программы</h3>
        <div class="graph__software">
          <img v-for="img in imgs" :src="img[0]" :alt="img[1]">
        </div>
      </div>
    </section>
  </main>
</template>

<script>
export default {
  setup() {
    const skills = [
      'Создание макетов простых сайтов',
      'Обработка растровых изображений (цветокоррекция; удаление фона; добавление деталей)',
      'Работа с векторной графикой (перевод иллюстраций, логотипов в векторные форматы, создание простых иллюстраций)',
      'Монтаж коротких видео',
    ]

    const softwares = [
      ['figma.svg', 'Figma'],
      ['AI.svg', 'Illustrator'],
      ['PS.svg', 'Photoshop'],
      ['AE.svg', 'AfterEffects'],
      ['PR.svg', 'PremierPro'],
    ]
    const imgs = []
    softwares.forEach(el => {
      imgs.push([
        require('@/assets/img/svg/hardskills/'+el[0]),
        el[1],
      ])
    })
  
    return {
      skills,
      imgs,
    }
  }
}
</script>

<style lang="scss">
.graph {
  .title-2 {
    margin-bottom: 20px;
  }
}
.graph__list {
  max-width: 800px;
  padding-left: 20px;
  color: var(--color-text);
  margin-bottom: 40px;
  li + li {
    margin-top: 10px;
  }
}

.graph__software {
  display: flex;
  gap: 20px;
  flex-wrap: wrap;
  img {
    height: 150px;
    width: 150px;
    object-fit: contain;
  }
}

@media (max-width: 600px) {
  .graph__software img {
    height: 100px;
    width: 100px;
  }
  .graph__list {
    font-size: 20px;
  }
}
@media (max-width: 400px) {
  .graph__list {
    font-size: 20px;
  }
}
  
</style>