<template>
  <div class="hardskills__card">
    <img :src="imgPath" :class="{'hardskills__img-crop': cropIcon}" />
    <h3>{{ title }}</h3>
    <b class="number">0{{ number }}</b>
    <div class="shadow"></div>
  </div>
</template>
<script>
export default {
  props: {
    number: String,
    svg: String,
    title: String,
    cropIcon: {
      type: Boolean,
      default: false,
    },
  },
  computed: {
    imgPath() {
      return require(`@/assets/img/svg/hardskills/${this.svg}.svg`);
    },
  },
};
</script>

<style lang="scss">
.hardskills__card {
  position: relative;
  text-decoration: none;
  display: flex;
  width: calc(33% - 17px);
  overflow: hidden;
  background-color: var(--color-dark2);
  height: 300px;
  padding: 20px;
  align-items: start;
  justify-content: space-between;
  transition: transform 0.8s ease, opacity 0.8s ease, background-color .3s;
  cursor: pointer;
  h3 {
    position: absolute;
    top: 20px;
    right: 20px;
    font-size: 24px;
  }
  img {
    width: 150px;
  }
  .number {
    position: absolute;
    font-size: 200px;
    font-style: italic;
    font-weight: 900;
    right: 20px;
    bottom: -80px;
    opacity: 0.3;
    transition: transform .2s;
  }
  .shadow {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 200px;
    background: var(--shadow-gradient);
  }
  .hardskills__img-crop {
    width: 100px;
  }
}

.hardskills__card:hover {
  .number {
    transform: translateY(10%);
  }
}

.hardskills__card:nth-child(1) {
  transition-delay: 0s;
}
.hardskills__card:nth-child(2) {
  transition-delay: .05s;
}
.hardskills__card:nth-child(3) {
  transition-delay: .1s;
}
.hardskills__card:nth-child(4) {
  transition-delay: .15s;
}
.hardskills__card:nth-child(5) {
  transition-delay: .2s;
}
.hardskills__card:nth-child(6) {
  transition-delay: .25s;
}

@media (max-width: 1150px) {
  .hardskills__card {
    width: calc(50% - 15px);
  }
}
@media (max-width: 800px) {
  .hardskills__card {
    width: 100%;
    max-width: 400px;
  }
}
@media (max-width: 430px) {
  .hardskills__card {
    height: 320px;
    img {
      margin-top: 40px;
    }
    .number {
      font-size: 180px;
      right: 15px;
      bottom: -85px;
    }
  }
}
</style>
