<template>
  <div class="project__item">
    <div class="preview" @click="$emit('openProject')">
      <img :src="imgSrc" :alt="alt" :style="{maxWidth: maxWidth+'px'}">
    </div>
    <p>
      <slot></slot>
    </p>
    <BtnMain @click="$emit('openProject')">Подробнее</BtnMain>
  </div>
</template>

<script>
import { computed } from 'vue';
export default {
  props: {
    img: String,
    alt: String,
    maxWidth: [String, Number],
  },

  setup(props) {
    const imgSrc = computed(()=>require('@/assets/img/projects/'+props.img))
    return {
      imgSrc
    }
  }
}
</script>

<style lang="scss">
.project__item {
  width: calc(33% - 17px);;
  display: flex;
  flex-direction: column;
  align-items: start;
  .preview {
    background-color: var(--color-dark2);
    width: 100%;
    height: 350px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 20px;
    padding: 60px;
    cursor: pointer;
    img {
      object-fit: contain;
      width: 100%;
    }
  }
  p {
    margin-bottom: 20px;
  }
  button {
    margin-top: auto;
  }
}
@media (max-width: 1150px) {
  .project__item {
    width: calc(50% - 15px);
  }
}
@media (max-width: 800px) {
  .project__item {
    width: 100%;
  }
}
@media (max-width: 500px) {
  .project__item .preview {
    padding: 20px;
    height: 300px;
  }
}


</style>