<template>
  <header class="header">
    <div class="header__arrow" :class="{ active: !homePage }">
      <img @click="backHome" src="@/assets/img/svg/arrow-left.svg">
    </div>
    <BtnBurger @click="$emit('switchMenu')" :active="menuActive" />
    <div class="header__back">
      <span @click="backHome" :class="{ active: homePage }">Вернуться</span>
    </div>

    <div class="header__socials">
      <a href="https://web.telegram.org/k/#@EclipseDanila" target="_blank">
        <img src="@/assets/img/svg/socials/telegram.svg" alt="Telegram" />
      </a>
      <a href="https://vk.com/id600245874" target="_blank">
        <img src="@/assets/img/svg/socials/vk.svg" alt="Vkontakte" />
      </a>
      <a href="https://github.com/EclipseDanila?tab=stars" target="_blank">
        <img src="@/assets/img/svg/socials/github.svg" alt="GitHub" />
      </a>
    </div>
  </header>
</template>

<script>
import { useRoute, useRouter } from 'vue-router'
import { ref, watch } from 'vue'
import BtnBurger from "@/components/UI/BtnBurger.vue";

export default {
  components: {
    BtnBurger,
  },
  props: {
    menuActive: Boolean,
  },
  setup() {
    const route = useRoute()
    const router = useRouter()
    const homePage = ref(false)

    watch(route, (value) => {
      if (value.name === 'home' || value.path === '/') {
        homePage.value = true
      } else {
        homePage.value = false
      }
    });

    function backHome() {
      if (route.meta.category) {
        console.log('#' + route.meta.category);
        router.push({ name: 'home', hash: '#' + route.meta.category })
      } else {
        router.push('/')
      }
    }

    return {
      homePage,
      backHome,
    }
  },

};
</script>

<style lang="scss">
.header {
  padding-bottom: 10px;
  padding-top: 10px;
  position: absolute;
  width: 60px;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 999;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: start;
}

.header__socials {
  margin-top: auto;
  display: flex;
  flex-direction: column;
  gap: 10px;

  img {
    width: 40px;
  }
}

.header__back {
  display: block;
  transform: rotate(-90deg) translateX(-100%);
  margin-top: -65px;
  overflow: hidden;

  span {
    display: block;
    transition: transform 0.3s;
  }

  span.active {
    transform: translateY(-100%);
  }
}

.header__arrow {
  display: none;
  height: 30px;
  overflow: hidden;

  img {
    padding-left: 5px;
    transition: transform .2s;
    height: 100%;
    object-fit: contain;
    transform: translateX(-100%);
  }
}

.header__arrow.active img {
  transform: none;
}


@media (max-width: 600px) {
  .header {
    width: 50px;
  }

  .header__socials img {
    width: 35px;
  }

  .header__back {
    font-size: 20px;
    margin-top: -55px;
    margin-right: 5px;
  }
}

@media (max-width: 500px) {
  .header {
    height: 50px;
    width: 100%;
    flex-direction: row;
    justify-content: space-between;
    padding: 0 10px;
    background-color: var(--color-dark1);
  }

  .header__socials {
    display: none;
  }

  .header__back {
    display: none;
  }

  .header__arrow {
    display: block;
  }
}

@media (min-width: 500px) {
  @media (max-height: 375px) {
    .header__back {
      font-size: 20px;
      margin-top: -55px;
      margin-right: 5px;
    }

    .header__socials {
      gap: 5px;

      img {
        width: 35px;
      }
    }
  }
}</style>
