<template>
  <div class="container functional_slider">
    <picture
    class="slide"
    :class="{
      'active': activeSlide === slide.id,
      'rounded': rounded
    }"
    v-for="slide in sliderArr">
      <source
      v-for="img, index in slide.srcSet"
      :media="`(max-width: ${sizes[index]}px)`"
      :srcset="img">
      <img :src="slide.img">
    </picture>
  </div>
  <div class="container functional_slider__info">
    <p
    v-for="slide in sliderArr"
    @click="switchSlide(slide.id)"
    :class="{'active': activeSlide === slide.id}">
    {{ slide.body }}
    </p>
  </div>
</template>

<script>
export default {
  props: {
    sliderArr: {
      required: true,
      type: Object,
    },
    sizes: {
      required: true,
      type: Array,
    },
    rounded: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      activeSlide: 1,
    }
  },
  methods: {
    switchSlide(id) {
      this.activeSlide = id
    }
  }
}
</script>

<style lang="scss">
.functional_slider {
  position: relative;
  width: 100%;
  display: flex;
  justify-content: center;
}

.slide {
  width: 100%;
  position: absolute;
  opacity: 0;
  transition: opacity .3s;
  z-index: 1;
  top: 0px;

  &.rounded {
    source, img {
      border-radius: 10px;
    }
  }

  img {
    width: 100%;
  }

  &.active {
    z-index: 2;
    opacity: 1;
  }

  &:nth-child(2) {
    position: static;
  }
}

.functional_slider__info {
  display: flex;
  justify-content: space-between;
  padding-top: 10px;
  gap: 20px;
  flex-wrap: wrap;
  p {
    display: block;
    position: relative;
    padding-top: 10px;
    width: calc(25% - 15px);
    transition: color .3s;
    &::after {
      content: '';
      transition: background-color .3s;
      position: absolute;
      height: 3px;
      background-color: transparent;
      top: 0;
      left: 0;
      right: 0;
      width: 100%;
    }
  }

  p.active {
    color: #fff;
    &::after {
      background-color: #fff;
    }
  }
}

@media (max-width:850px) {
  .functional_slider__info p {
    width: calc(50% - 10px);
  }
}
@media (max-width:550px) {
  .functional_slider__info p {
    max-width: 300px;
    width: 100%;
  }
}
</style>